.contact {
  background-color: $light-grey;
  display: flex;
  justify-content: center;
  font-weight: 500;
  font-family: $lato;
  padding: 50px 0px 50px 0px;
}
.contact__header {
  width: 100%;
  margin-bottom: 30px;
}
.contact__info {
  margin: 10px 0px 10px 0px;
  display: flex;
  align-self: flex-start;
  width: 85vw;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact__social_network {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  transition: all $transition-time ease-in-out;
  & a > i {
    font-size: 40px;
    color: black;
    margin: 0px 10px 0px 10px;
    transition: all $transition-time ease-in-out;
  }
  & a {
    font-weight: bolder;
    font-family: $lato;
    font-size: 20px;
    margin: 20px 50px 10px 50px;
    transition: all $transition-time ease-in-out;
    &:hover {
      // text-shadow: 0px 0px 2px rgb(22, 22, 22);
      color: lighten($color: $new-grey, $amount: 3);
    }
  }
}
.contact__email {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact__header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $new-grey;
  font-family: $lato;
  text-transform: capitalize;
  transition: all $transition-time ease-in-out;
  & h1 {
    font-size: 25px;
    transition: all $transition-time ease-in-out;
  }
}

@media only screen and (max-width: 1024px) {
  .contact__header {
    & h1 {
      font-size: 30px;
    }
  }
  .contact__social_network {
    & a > i {
      font-size: 35px;
    }
    & a {
      font-size: 20px;
    }
  }
}
@media only screen and (max-width: 800px) {
  .contact__header {
    & h1 {
      font-size: 20px;
    }
  }
}
@media only screen and (max-width: 600px) {
  .work__header {
    & h1 {
      font-size: 20px;
    }
  }
  .contact__social_network {
    display: flex;
    flex-direction: column;
    align-items: center;
    & a > i {
      font-size: 33px;
    }
    & a {
      font-size: 16px;
      margin: 10px 0px 10px 0px;
      padding: 0px;
    }
  }
}
