.resume {
  background-color: $light-grey;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 50px 0px 50px 0px;
}
.resume__container {
  width: 85vw;
  margin: 10px 0px 30px 0px;
  color: $new-grey;
  font-family: $lato;
  font-weight: 500;
  text-transform: capitalize;
  transition: all $transition-time ease-in-out;
  & h1 {
    text-align: center;
    font-size: 25px;
    margin-bottom: 30px;
    transition: all $transition-time ease-in-out;
  }
}
.resume__me {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex: 1;
  margin: 30px 0px 30px 0px;
}
.resume__img {
  //   width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;

  & img {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 150px;
    height: 140px;
    border-radius: 50%;
  }
}
.resume__desc {
  color: black;
  width: fit-content;
  width: 45%;
  & p {
    font-family: $lato;
    font-weight: 100;
    font-size: 18px;
    text-align: left;
    text-justify: inter-word;
    margin: 0;
  }
}

@media only screen and (max-width: 1024px) {
  .resume__container {
    & h1 {
      font-size: 30px;
    }
  }
  .resume__img {
    & img {
      width: 130px;
      height: 120px;
    }
  }
  .resume__desc {
    & p {
      font-size: 15px;
    }
  }
}
@media only screen and (max-width: 800px) {
  .resume__container {
    & h1 {
      font-size: 24px;
    }
  }
  .resume__img {
    & img {
      width: 110px;
      height: 100px;
    }
  }
  .resume__desc {
    & p {
      font-size: 13px;
      text-align: justify;
    }
  }
}
@media only screen and (max-width: 600px) {
  .resume__container {
    & h1 {
      font-size: 20px;
    }
  }
  .resume__desc {
    & p {
      font-size: 12px;
      text-align: justify;
    }
  }
  .resume__img {
    & img {
      width: 100px;
      height: 90px;
    }
  }
}
