.arrow {
  position: fixed;
  right: 0px;
  bottom: 0px;
  margin: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #0f0f0f;
  z-index: 100;
  text-align: center;
  font-size: 40px;
  color: $light-grey;
  animation: bounce;
  animation-duration: 2s;
  & a {
    position: relative;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 1024px) {
  .arrow {
    font-size: 38px;
    width: 48px;
    height: 48px;
    right: 0px;
    bottom: 0px;
  }
}
@media only screen and (max-width: 800px) {
  .arrow {
    font-size: 35px;
    width: 45px;
    height: 45px;
    right: -20px;
    bottom: 0px;
  }
}
@media only screen and (max-width: 600px) {
  .arrow {
    font-size: 30px;
    width: 40px;
    height: 40px;
    right: -40px;
    bottom: -30px;
  }
}
