.header__nav {
  margin: 0px;
  padding: 0px;
  position: relative;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  transition: all $transition-time ease-in-out;
  // mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.973), rgba(0, 0, 0, 0.589));
}
.header__nav_manu {
  align-items: center;
  display: flex;
  position: relative;
  top: 20px;
  padding: 20px 0px 20px 30px;
}
.navbar {
  position: relative;
  width: 100%;
}
.navbar__menu {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $montserrat;
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 0.85em;
  line-height: 30px;
  letter-spacing: $letter-space;
  width: 95%;
  & li {
    color: $white;
    // text-shadow: 0px 0px 10px black;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.9);
    display: inline-block;
    margin: 0px 50px 0px 50px;
    text-decoration: none;
    // -webkit-text-stroke: 0.2px black;
    transition: all $transition-time ease-in-out;
    &:hover {
      // color: $hover-color;
      color: lighten($color: $new-grey, $amount: 3);
      text-shadow: 0px 0px 0px $light-grey;
    }
  }
}
.header__nav_ham_open {
  display: none;
}
.header__nav_ham_close {
  display: none;
}
.header__logo {
  color: $white;
  font-weight: 500;
  font-size: 30px;
  font-family: $yellowtail;
  letter-spacing: $letter-space-logo;
  text-shadow: 1px 1px 5px $light-grey;
  transition: all $transition-time ease-in-out;
  &:hover {
    // color: $hover-color;
    color: lighten($color: $new-grey, $amount: 3);
    text-shadow: 0px 0px 2px $light-grey;
  }
}
.header__presentation {
  margin: 0;
  padding: 0px;
  height: 80%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header__banner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $white;
  font-family: $lato;
  font-weight: 500;
  text-transform: capitalize;
  transition: all $transition-time ease-in-out;
  top: -15px;
  & h1 {
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.9);
    font-size: 7vw;
  }
  & h3 {
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.6);
    margin-top: 10px;
    font-size: 2vw;
  }
}
.header__banner_line {
  position: relative;
  width: 9.1em;
  margin: 0 auto;
  border-right: 3px solid lighten($color: $dark-grey, $amount: 10);
  white-space: nowrap;
  overflow: hidden;
}

/* Animation */

.header__baner_line_anim {
  // animation-timing-function: ease-in-out;
  animation: typewriter 3s steps(60) 1s 1 normal both,
    blinkTextCursor 0.6s steps(60) infinite normal;
  // animation: blinkTextCursor 0.6s steps(60) infinite normal;
  // -webkit-animation-name: typewriter;
  // -webkit-animation-duration: 2s;
  // -webkit-animation-iteration-count: infinite;
  // -webkit-animation-timing-function: steps(60);
  // -webkit-animation-delay: 30s;
}

@keyframes typewriter {
  0% {
    width: 0;
  }
  25% {
    width: 2.2em;
  }
  50% {
    width: 4.1em;
  }
  100% {
    width: 9.2em;
  }
}
@keyframes blinkTextCursor {
  from {
    border-right-color: $dark-grey;
  }
  to {
    border-right-color: transparent;
  }
}

/* Responsive design */

@media only screen and (max-width: 1024px) {
  .header__logo {
    text-shadow: 0px 0px 3px $light-grey;
    position: relative;
    font-size: 25px;
    // left: 8px;
  }
  .navbar__menu {
    font-size: 0.75em;
    & li {
      margin: 0px 45px 0px 45px;
    }
  }
  .header__banner {
    & h1 {
      font-size: 8vw;
    }
    & h3 {
      margin-top: 10px;
      font-size: 3vw;
    }
  }
}

@media only screen and (max-width: 800px) {
  .header__logo {
    text-shadow: 0px 0px 3px $light-grey;
    position: relative;
    font-size: 18px;
    left: 5px;
  }
  .navbar__menu {
    font-size: 0.65em;
    & li {
      margin: 0px 30px 0px 30px;
    }
  }
  .header__banner {
    & h1 {
      font-size: 8vw;
    }
    & h3 {
      margin-top: 10px;
      font-size: 3vw;
    }
  }
}
@media only screen and (max-width: 600px) {
  .header__logo {
    text-shadow: 0px 0px 2px $light-grey;
    position: relative;
    font-size: 18px;
    left: 3px;
  }
  .navbar__menu {
    z-index: 20;
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    right: 0px;
    & li {
      margin: 0px 0px 5px 0px;
    }
  }
  .header__nav {
    height: 70vh;
  }
  .header__banner {
    & h1 {
      font-size: 11vw;
    }
    & h3 {
      margin-top: 10px;
      font-size: 4vw;
    }
  }
  .header__nav_ham_open {
    color: $white;
    font-size: 1.2em;
    padding: 4px;
    text-shadow: 0px 0px 2px $light-grey;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all $transition-time ease-in-out;
  }
  .header__nav_ham_close {
    color: $white;
    font-size: 1.3em;
    padding: 4px;
    text-shadow: 0px 0px 2px $light-grey;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all $transition-time ease-in-out;
  }
  .header__nav_manu {
    padding: 30px 30px 20px 20px;
  }
}
