.cards {
  margin-top: 25px;
  max-height: 490px;
  overflow-y: auto;
}
.cards__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card__view {
  display: none;
  animation-duration: $transition-time;
}
.card {
  position: relative;
  margin-top: 15px;
  max-width: 250px;
  margin: 10px 20px 10px 20px;
  width: 230px;
  display: flex;
  flex-direction: column;
  border: 2px solid rgba(95, 97, 97, 0.103);
  -webkit-box-shadow: 0px 6px 9px -3px rgba(95, 97, 97, 0.103);
  -moz-box-shadow: 0px 6px 9px -3px rgba(95, 97, 97, 0.103);
  box-shadow: 0px 8px 7px -5px rgba(95, 97, 97, 0.103);
  // box-shadow: 0px 6px 9px -3px rgba(0, 0, 0, 0.295);
  border-radius: 5px;
  transition: all $transition-time ease-in-out;
  &:hover .card__view {
    background-color: rgba(0, 0, 0, 0.671);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: absolute;
    width: 100%;
    height: 100%;
    & h3 {
      font-size: 10px;
      font-family: $lato;
      color: $white;
    }
    & i {
      font-size: 45px;
      color: $white;
      transition: color $transition-time ease-in-out;
      &:hover {
        color: black;
        text-shadow: 0px 0px 5px $light-grey;
      }
    }
    & a {
      margin: 25px;
    }
  }
}
.card__image {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  // filter: brightness(1);
  filter: contrast(190%);
  // filter: saturate(5);
  border-radius: 3px;
  width: 100%;
  height: 129px;
}
.card__content {
  padding: 15px 15px;
  & h3 {
    font-family: $lato;
  }
}

.card__title {
  letter-spacing: 1px;
  line-height: 24px;
  margin-bottom: 10px;
  font-family: $lato;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: lighten($color: $new-grey, $amount: 10);
  // transition: font-size $transition-time;
}

@media only screen and (max-width: 1024px) {
  .card__title {
    font-size: 18px;
  }
  .card {
    width: 220px;
    margin-top: 5px;
    &:hover .card__view {
      & i {
        font-size: 40px;
      }
      & a {
        margin: 30px;
      }
    }
  }
  .card__image {
    height: 118px;
  }
}
@media only screen and (max-width: 800px) {
  .card__title {
    font-size: 16px;
  }
  .card {
    width: 210px;
    margin-top: 5px;
    &:hover .card__view {
      & i {
        font-size: 40px;
      }
      & a {
        margin: 30px;
      }
    }
  }
  .card__image {
    height: 108px;
  }
}
@media only screen and (max-width: 600px) {
  .card__title {
    font-size: 16px;
  }
  .card {
    width: 100%;
    height: 100%;
    margin-top: 5px;
    &:hover .card__view {
      & i {
        font-size: 45px;
      }
      & a {
        margin: 25px;
      }
    }
  }
  .card__image {
    height: 108px;
  }
}
