.work {
  margin: 0px;
  padding: 50px 0px 50px 0px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.work__container {
  width: 85vw;
  margin: 10px 0px 30px 0px;
}
.work__ul {
  width: 100%;
}
.work__header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $new-grey;
  font-family: $lato;
  font-weight: 500;
  text-transform: capitalize;
  transition: all $transition-time ease-in-out;
  & h1 {
    font-size: 25px;
    margin-bottom: 30px;
    transition: all $transition-time ease-in-out;
  }
}

@media only screen and (max-width: 1024px) {
  .work__header {
    & h1 {
      font-size: 30px;
    }
  }
}
@media only screen and (max-width: 800px) {
  .work__header {
    & h1 {
      font-size: 24px;
    }
  }
}
@media only screen and (max-width: 600px) {
  .work__header {
    & h1 {
      font-size: 20px;
    }
  }
}
