* {
  margin: 0px;
  padding: 0px;
}

html,
body {
  margin: 0px;
  padding: 0px;
  background-color: #0f0f0f;
  // scroll-behavior: smooth;
  position: relative;
}
.mouse {
  cursor: pointer;
}
.links {
  text-decoration: none;
  color: inherit;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}
@media only screen and (max-width: 600px) {
  ::-webkit-scrollbar {
    width: 3px;
  }
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px rgba(128, 128, 128, 0);
  // border-radius: 20px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $new-grey;
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: lighten($color: $new-grey, $amount: 10);
}
